.chat-story-view .chat-story-title {
  -ms-flex: 0 0 55px;
      flex: 0 0 55px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.chat-story-view h4 {
  margin: 0;
  font-weight: normal;
  font-size: 18px;
}

.chat-story-view .chat-story-view-advice {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.chat-story-view .chat-story-view-advice h1 {
  color: #AAA;
  text-align: center;
}

.chat-story-view .chat-story-messages {
  padding: 25px 0;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
}

.chat-typing-dots {
  margin: 25px 0 0 50px;
  width: 10px;
  height: 10px;
  position: relative;
}

.chat-typing-dots div,
.chat-typing-dots::before,
.chat-typing-dots::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #333;
}

.chat-typing-dots::before {
  right: calc(100% + 5px);
  -webkit-animation: loading 0.9s infinite;
          animation: loading 0.9s infinite;
}

.chat-typing-dots div {
  left: 0;
  -webkit-animation: loading 0.9s 0.15s infinite;
          animation: loading 0.9s 0.15s infinite;
}

.chat-typing-dots::after {
  left: calc(100% + 5px);
  -webkit-animation: loading 0.9s 0.3s infinite;
          animation: loading 0.9s 0.3s infinite;
}

@-webkit-keyframes loading {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0.7;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes loading {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0.7;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}



